<template>
  <ul>
    <li v-for="item in pageArticleData" :key="item._id">
      <div class="home">
        <header>
          <h2>{{ item.title }}</h2>
          <div class="date">
            <p class="day">{{ new Date(item.date).getDate() }}</p>
            <p class="month">
              <span class="months">{{
                new Date(item.date).getMonth() + 1
              }}</span>
              <span>{{ new Date(item.date).getFullYear() }}</span>
            </p>
          </div>
        </header>
        <div class="actile-content">
          <div class="imgcover">
            <img :src="baseUrl + item.coverUrl" alt="" />
          </div>
          <div class="describe">
            <p class="desctason">{{ item.describe }}</p>
            <div class="other">
              <p>阅读数：{{ item.readingNum }}</p>
              <router-link :to="`/actire/${item._id}`">点击阅读</router-link>
            </div>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  name: "actire",
  data() {
    return {
      pageArticleData: [], //文章数据
    };
  },
  methods: {
    async getArticles() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/article",
      });
      console.log(data);
      this.pageArticleData = data.data;
    },
  },
  created() {
    this.getArticles(); //创建项目时调用
  },
};
</script>

<style lang="less" scoped>
.home {
  width: 100%;
  height: 370px;
  background-color: #fff;
  box-shadow: 0 0 4px #ddd;
  header {
    font-size: 20px;
    display: flex;
    width: 100%;
    // line-height: 29px;
    position: relative;
    padding-top: 32px;
    margin-top: 20px;
    h2 {
      margin-bottom: 15px;
      border-left: 5px solid #73b899;
      font-size: 20px;
      letter-spacing: 3px;
      line-height: 24px;
      text-indent: 10px;
    }
    .date {
      position: absolute;
      // margin-right: 12px;
      right: 15px;
      .day {
        font-weight: bolder;
        font-size: 40px;
        text-align: center;
        color: #6bc30d;
      }
      .month {
        .months {
          color: #6bc30d;
          padding-right: 4px;
        }
      }
    }
    // font-weight: bold;
  }
  .actile-content {
    width: 100%;
    height: 82%;
    display: flex;
    margin-top: 27px;
    .imgcover {
      height: 270px;
      // outline: 1px black solid;
      width: 50%;
      img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
      }
    }
    .describe {
      height: 100%;
      width: 50%;
      position: relative;
      .desctason {
        position: absolute;
        padding-left: 12px;
      }
      .other {
        position: absolute;
        right: 14px;
        top: 230px;
        p {
          position: relative;
          top: -10px;
          color: #aaa;
          font-size: 12px;
        }
        a {
          padding: 6px 10px;
          background-color: #409eff;
          color: #fff;
          font-size: 14px;
          transition: opacity 0.3s;
          &:hover {
            opacity: 0.8;
          }
        }
      }
    }
  }
}
</style>